body {
	/* background-color: #4c566f; */
	background-color: #ffffff;
	margin: 0;
	font-family: 'TT Commons', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

@font-face {
	font-family: 'TT Commons';
	src: url('../src/fonts/TTCommons-ThinItalic.eot');
	src: local('TT Commons Thin Italic'), local('TTCommons-ThinItalic'),
		url('../src/fonts/TTCommons-ThinItalic.eot?#iefix') format('embedded-opentype'),
		url('../src/fonts/TTCommons-ThinItalic.woff') format('woff'), url('../src/fonts/TTCommons-ThinItalic.ttf') format('truetype');
	font-weight: 100;
	font-style: italic;
}

