.container {
	width: 100%;
	display: flex;
	justify-content: center;
	box-sizing: border-box;
	padding: 20px 0;
}

.content {
	display: flex;
	flex-direction: row;
	align-items: flex-start;
	justify-content: flex-start;
	max-width: 800px;
	width: 100%;
	padding: 0 20px;
}

.link {
	line-height: 34px;
	margin-right: 120px;
}

.link:first-child {
	margin-left: 20px;
}

.font {
	font-family: 'TT Commons' !important;
}

@media screen and (max-width: 600px) {
	.container {
		padding: 40px 20px;
	}
	.content {
		flex-direction: column;
		align-items: flex-start;
		padding: 0;
	}
	.link {
		margin-bottom: 20px !important;
		margin-right: 0;
	}
	.link:first-child {
		margin-left: 0;
	}
	.link:last-child {
		margin-bottom: 0 !important;
	}
}
